@import url(https://fonts.googleapis.com/css2?family=Poppins:ital@1&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;

}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App{text-align:center}.request-container{display:flex;justify-content:center}.request-container__btn{cursor:pointer}

body{background-color:#EBECF0}.list-container{display:flex;justify-content:center;flex-direction:column}.title{margin-bottom:2rem;font-size:2rem;letter-spacing:0.1em;font-weight:700;text-align:center;color:#31456a;text-shadow:1px 1px 1px #fff}.list{font-family:'Poppins', sans-serif;box-shadow:-5px -5px 20px #fff,5px 5px 20px #BABECC;-webkit-padding-start:0px;padding-inline-start:0px;border-radius:20px;width:50vw;margin:auto;margin-top:40px}.details{display:flex;flex-basis:20%;justify-content:space-between}a{text-shadow:1px 1px 1px #fff;color:#31456a;text-decoration:none}.list__elem{list-style:none;text-shadow:1px 1px 1px #fff;color:#31456a;display:flex;justify-content:space-between;padding:11px 20px 11px 20px;font-size:18px;font-weight:500;flex-basis:80%;border-bottom:solid 1px lightgray}.list__elem:last-child{border-bottom:none}.list__elem__link:hover{cursor:pointer}.list__elem:active{box-shadow:inset 1px 1px 2px #BABECC,inset -1px -1px 2px #fff}.list__elem:active:first-child{border-top-left-radius:20px;border-top-right-radius:20px}.list__elem:active:last-child{border-bottom-left-radius:20px;border-bottom-right-radius:20px}.list__elem .wrap{display:inline-flex;flex-wrap:wrap;margin-left:130px}.list__elem .wrap li{margin:0px 5px 0px 0px}.container,.request-container{display:flex;justify-content:space-around;width:40vw;margin:auto}.container__btn,.request-container__btn{width:15vw;height:30px;border:none;outline:none;border-radius:20px;background:#EBECF0;box-shadow:-5px -5px 20px #fff,5px 5px 20px #BABECC;font-size:18px;margin-top:20px;letter-spacing:0.02em;color:#da1e1e;cursor:pointer;transition:all 0.2s ease-in-out}.container__btn:hover,.request-container__btn:hover{box-shadow:-2px -2px 5px #fff,2px 2px 5px #BABECC}.container__btn:active,.request-container__btn:active{box-shadow:inset 1px 1px 2px #fff,inset -1px -1px 2px #BABECC}.container__btn--search,.request-container__btn--search{width:18vw}.input{width:200px;height:10px;padding:1rem;border:none;outline:none;border-radius:2rem;background:#EBECF0;box-shadow:inset 2px 2px 5px #BABECC,inset -5px -5px 10px #fff;color:black;font-size:1rem;letter-spacing:0.1em;font-family:'Noto Sans TC', sans-serif;transition:all 0.2s ease-in-out}.input::-webkit-input-placeholder{font-family:'Noto Sans TC', sans-serif;color:rgba(218,15,15,0.5);font-size:14px;text-shadow:1px 1px 1px #fff}.input:-ms-input-placeholder{font-family:'Noto Sans TC', sans-serif;color:rgba(218,15,15,0.5);font-size:14px;text-shadow:1px 1px 1px #fff}.input::-ms-input-placeholder{font-family:'Noto Sans TC', sans-serif;color:rgba(218,15,15,0.5);font-size:14px;text-shadow:1px 1px 1px #fff}.input::placeholder{font-family:'Noto Sans TC', sans-serif;color:rgba(218,15,15,0.5);font-size:14px;text-shadow:1px 1px 1px #fff}.input:focus{box-shadow:inset 1px 1px 2px #BABECC,inset -1px -1px 2px #fff}

.title{text-align:center}

